@import url("https://fonts.googleapis.com/css?family=Roboto:400,400i,700");
body{
  overflow-x: hidden;
  font-family: Roboto, sans-serif;
}

#Header {
  text-align: center;
  width: 100vw;
  height: 120px;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #CCCC99;
}
#Header:hover {
  background-color: #CCCC99;
}

#Left_icons{
  text-align: left;
  width: 20%;
  height: 50%;
  padding-left: 0.5%;
  padding-right: 14.5%;
  padding-top: 30px;
}
#Right_icons{
  text-align: right;
  width: 33%;
  height: 50%;
  padding-left: 3%;
  padding-top: 30px;
  padding-right:0.5%;
}
#Right_icons >a >img{
  padding: 0.5%;
}
