#ProductThumnail {
    text-align: center;
    width: 280px;
    height: 500px;
    padding: 1%;
}

.img-box{
    width: 250px;
    height: 350px;
  }
  .img-box img{
    width: 250px;
    height: 350px;
    opacity: 1;
  }
  .img-box img.img-hover{
    display:none;
  }
  .img-box:hover img.img{
    display:none;
  }
  .img-box:hover img.img-hover{
    display:block;
  }