#SideList {
    background-color: #CCCC99;
    height: 100%;
    width: 300px;
    position: fixed;
    transition: 0.5s;
}

.List_icons {
    width: 30px;
    height: 60px;
    position: absolute;
    left: 4.5%;
    top: 7.7%;
}
#SideList a {
    text-decoration: none;
    color: #101010;
}

#SideList a:hover {
    color: #FFFFFF;
}

.sideMenuList {
    width: 300px;
    color: #101010;
    margin-top: 30%;
}
.sideMenuList ul{
    list-style: none;
    text-align: left;
}
.sideMenuList ul li{
    padding: 5%;
}
.sideMenuList a >#BITGLOOMY{
    padding-top: 10%;
    width: 70%;
    height: auto;
}