#LogIn {
    width: 100vw;
    padding-top: 25vh;
    font-size: 18px;
}

.LogInForm{
    width: 100%;
}

.LogInForm ul{
    margin: 0;
    padding: 0;
    list-style: none;
}

.LogInForm li{
    padding: 0;
}

input{
    margin: 0.5%;
    padding-left: 1%;
    width: 77%;
    height: 50px;
    font-size: 16px;
}

.checkForm {
    width: 78%;
    text-align: left;
    color: #101010;
}
.checkForm >a{
    float: right;
    padding-top: 1%;
}
.checkForm >label >input{
    width: 20px;
    height: 20px;
}
.checkForm >label:hover{
    color: gray;
}

.LogInBtn{
    margin: 0;
    padding: 0;
    width: 78%;
}

button{
    margin-top: 1%;
    width: 100%;
    height: 50px;
    background-color: black;
    color: white;
    border-radius: 24px;
    font-size: 20px;
}

.SignDiv{
    display: block;
    margin-top: 20px;
}
.SignDiv >a{
    padding: 20px;
}

.EasyLogIn{
    margin-top: 60px;
    width: 80%;
    height: 200px;
    border-top: 1px solid gray;
}
.EasyLogIn >.EasyLogInIcons{
    padding: 10px;
}
.EasyLogInIcons >a{
    padding: 10px;
}
.EasyLogIn >p {
    background-color:#F5F5DC;
    width: 100%;
}

.naverIcon{
    width: 55px;
    height: 55px;
}
.kakaoIcon{
    width: 60px;
    height: 60px;
}
.googleIcon{
    width: 60px;
    height: 60px;
}